import { Component, OnInit,Injectable } from '@angular/core';
import {Router, ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import {Sloka, SlokaService } from '../sloka-service.service';

import {Summary} from '../summary';

@Component({
  selector: 'app-slokas',
  templateUrl: './slokas.component.html',
  styleUrls: ['./slokas.component.css']
})
export class SlokasComponent implements OnInit {

    slokas: Sloka[];
    summary: Summary[];
  //  errorMessage:error<any>;

  constructor(
    private router : Router,
    private service : SlokaService
    ) 
    {}

  ngOnInit() {
  
    this.slokas = this.service.getSlokas();
    this.service.getSummary(); //.subscribe(summary => this.summary = summary, error => this.errorMessage = error<any>);
  
  }
  
  onSelect(sloka:Sloka){
        if(sloka.enabled){
            this.router.navigate(['/canto/'+sloka.id]);
        }
  }
  
  isSelected(sloka){
    //return true; 
          return sloka.enabled === true; //? 'Click to View Canto information' : 'No info available for canto'+sloka.id;
  }
  

}



