import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule , JsonpModule} from '@angular/http';
import { MaterialModule } from '@angular/material';
import { RouterModule, Routes } from '@angular/router';
//import { DynamicHTMLModule } from '
import { NgbModule }  from '@ng-bootstrap/ng-bootstrap'
//import { MarkdownIt } from '@types/markdown-it';
import { Ng2FilterPipeModule } from 'ng2-filter-pipe';
import { Ng2SearchPipeModule } from 'ng2-search-filter';

import { AppComponent } from './app.component';
import { ContactComponent } from './contact/contact.component';
import { SlokasComponent } from './slokas/slokas.component';
import { SlokaDetailsComponent } from './sloka-details/sloka-details.component';
import { HomeComponent } from './home/home.component';


import { AboutComponent } from './about/about.component';
import { AboutComponentComponent } from './about-component/about-component.component';
import { PrefaceComponent } from './preface/preface.component';
import { PoetComponent } from './poet/poet.component';
import { SummaryComponent } from './summary/summary.component';
import { SourceComponent } from './source/source.component';

/** child component **/

import { SlokaDetailView } from  './sloka-details/sloka-detail-child.component';

/** service imports **/
import { SlokaService } from './sloka-service.service';
import { SummaryService } from './summary.service';
import 'hammerjs';
import { DictViewComponent } from './dict-view/dict-view.component';
//import 'MarkdownIt';


const appRoutes: Routes = [
{ path: 'home', component: HomeComponent},
{ path: 'contact', component:ContactComponent},
{ path: 'canto', component:SlokasComponent},
{ path: 'canto/:id', component:SlokaDetailsComponent },
{ path: 'about', component:AboutComponent },
{ path: 'preface', component:PrefaceComponent },
{ path: 'summary', component:SummaryComponent },
{ path: 'source', component:SourceComponent },
{ path: 'poet', component:PoetComponent },
{ path: '**', redirectTo: '/home', pathMatch: 'full' },
{ path: '', redirectTo: '/home', pathMatch: 'full' }
]

@NgModule({
  declarations: [
    AppComponent,
    ContactComponent,
    SlokasComponent,
    SlokaDetailsComponent,
    HomeComponent,
    AboutComponent,
    PrefaceComponent,
    PoetComponent,
    SummaryComponent,
    SourceComponent,
    SlokaDetailView,
    DictViewComponent,
	AboutComponentComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterialModule,
    RouterModule.forRoot(appRoutes,{ useHash: true }),
    NgbModule.forRoot(),
    JsonpModule,
    Ng2FilterPipeModule,
	Ng2SearchPipeModule    
  ],
  providers: [SlokaService, SummaryService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



