let slokasPromise = Promise.resolve(SLOKAS);


@Injectable()
export class SlokaService {

    private cantoUrl = 'assets/json/canto';
    private indexUrl = 'assets/json/index';
    private summaryUrl = 'assets/json/text.json';
    private tanslationUrl = 'assets/json/SV-';
	private dictUrl = 'assets/dicturl.json';
    
    private cantoList:SlokaTest[];
	public dictUrlData:any;

  constructor(private http: Http ) {  
  }
  
  getDictUrl() {
	return this.http.get(this.dictUrl)
    .map(this.getJsonData)
    .catch(this.handleError);
  }
  
  getJsonData(res:Response) {
	 return res.json() || {};
  }

  
  getSlokas() { return SLOKAS};
  
  
  
  
  getCantoList(){
  
    return this.cantoList;
  }
  
  setCantoList(list:SlokaTest[]){
  
     this.cantoList = list;
  }
  
  
  
  getSlokaDetails(id: number ){
  
      return slokasPromise.then(slokas => slokas.find(sloka => sloka.id === +id));
    
  }
  
  getSummary():Observable<Summary[]>{
  
    return this.http.get(this.summaryUrl)
    .map(this.extractData)
    .catch(this.handleError);
  
  }
  
  getCantoData(num): Observable<SlokaTest[]>{
  
    let url = this.cantoUrl+num+".json";
    
    return this.http.get(url)
    .map(this.extractData)
    .catch(this.handleError);
    
    
  }
  
  getCangoIndex(num) : Observable<SlokaIndex[]>{
  
    let url = this.indexUrl+num+".json";
    return this.http.get(url)
    .map(this.extractData)
    .catch(this.handleError);
  
  }
  
  getSelectedSloka(sloka:SlokaIndex){
    
    //console.log(this.cantoList);
   // console.log(this.cantoList[sloka.slokaNo]);
    //console.log(sloka);
  
  }
  
// getTranslation details 

getSlokaTranslation(num):Observable<Table[]>{
    
    let url = this.tanslationUrl+num+".json";
    
     return this.http.get(url)
    .map(this.extractData)
    .catch(this.handleError);
    
}
  
  private extractData(res: Response){
  
    let body = res.json();
    //this.cantoList = body;
    return body || {};
  
  }
  
  private handleError(error:Response| any ){
    return  Observable.throw("error");
  }
  

}



