import { Component, ViewChild, ElementRef, OnInit, Pipe, PipeTransform } from '@angular/core';
import {Router, ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import {Sloka, SlokaDetails ,SlokaService } from '../sloka-service.service';
import 'rxjs/add/operator/switchMap';
import 'rxjs/Rx';

import { SlokaTest, SlokaIndex } from '../sloka';
import { SlokaDetailView } from './sloka-detail-child.component';
import { DictViewComponent } from '../dict-view/dict-view.component';
import { Table } from '../table';

 

@Component({
  selector: 'app-sloka-details',
  templateUrl: './sloka-details.component.html',
  styleUrls: ['./sloka-details.component.css']
  //directives : [SlokaDetailView]
})




export class SlokaDetailsComponent implements OnInit {


  public sloka:Sloka;
  public slokaDetails: SlokaDetails;
  public searchText :string;
  public slokaList:SlokaTest[];
  public canto: SlokaTest[];
  public index: SlokaIndex[];
  public errorMessage:string;
  public previouSloka:SlokaTest;
  public slokaSearch:any;
  
  public sub : any;
  public cantoNumber : number;
  public cantoInfo:string;
  public slokaNo:number;
  public loading:boolean;
    
  public title:string;
  public slokaTranslation:any;
    
  public userFilter: any = { name: '' };
  
  @ViewChild('input')
  input:ElementRef;
    
  @ViewChild(SlokaDetailView) slokaDetailView: SlokaDetailView

  constructor(public service: SlokaService, public router:Router,public route: ActivatedRoute) { }

  ngOnInit() {
  
  this.slokaSearch = {
	sloka:''
  };
  
  this.loading = true;
  
  /*let eventObservable = Observable.fromEvent(this.input.nativeElement, 'keyup')
      eventObservable.subscribe(); */
  
    //console.log(this.route.params['id']);
      
    this.route.params
    .switchMap((params: Params) => this.service.getSlokaDetails(+params['id']))
    .subscribe((sloka: Sloka) => this.sloka = sloka);
    
        this.route.params
        .switchMap((params:Params) => 
        this.service.getCantoData(params['id'])).subscribe(canto => this.canto = canto, 
                        error => this.errorMessage = <any>error
                      
        );
          this.loading = false;
    
        this.route.params
        .switchMap((params:Params)=>
            this.service.getCangoIndex(params['id'])).subscribe(index => this.index = index, error => this.errorMessage = <any>error);
    
        this.sub = this.route.params.subscribe(params => {
            this.cantoNumber = +params["id"];
            this.cantoInfo = "Canto"+this.cantoNumber;
        });
        
		this.getTranslationObject();
       
		if(!this.service.dictUrlData) {
			this.service.getDictUrl().subscribe(dictConfig => this.service.dictUrlData = dictConfig, error => this.errorMessage = error);
		} 
    
  }
  
  gotoSlokas(){
  let slokaId = null;
    this.router.navigate(['/canto',{id:slokaId }]);
  }
  
  
  isSelected(sloka){  
     
    return (this.slokaNo === +sloka.slokaNo);
  }
  
  
  selectSloka(sloka){
  
  if(this.slokaList == undefined)
        this.slokaList = [];
  
    if(this.slokaList != undefined && this.slokaList.length  ===  0 ){
        this.slokaList = this.canto;
    } 
    
   
        this.canto = Object.assign([], this.slokaList).filter(
      sloka => sloka.slokaNo === sloka.slokaNo
      )
    
        this.canto = [];
        let index = +sloka.slokaNo;
        this.slokaNo = index;
        
       this.canto.push(this.slokaList[index-1]);
  
  }
  
  showAll(){
  
     if(this.canto.length ==1){
         
        this.canto[0].show = false;
        this.canto = Object.assign([], this.slokaList);
        this.slokaNo = 0;
    }
    
  }
    
  showView(item : SlokaTest){
    
      this.resetExpandView(item); 
      this.previouSloka = item;
     item.show  = !item.show; 
	 setTimeout(function(){
		let num:number = +item.slokaNo;
		let el = document.getElementById(num > 0 ? num-1+'': num+'');
		if(item.show && el)
			el.scrollIntoView();
	 }.bind(this,item),1200);
	 
  }
    
resetExpandView(item : SlokaTest){
    
    if(this.previouSloka != undefined && item.slokaNo != this.previouSloka.slokaNo)
      this.previouSloka.show = false;
}
    
    
// getSlokaTranslation

getSlokaTranslation(id:string){
    
    let slokaNumber = "SV-"+id;

		return this.slokaTranslation.cantoList[slokaNumber];
        //return this.slokaTranslation[slokaNumber];
    
}

getAdditionalInfo(id:string, num:any) {
	return this.slokaTranslation.otherInfo["SV-"+id+"row"+num].join(" ");
}
    
getTranslationObject(){
    
    
    this.route.params
        .switchMap((params:Params) => 
        this.service.getSlokaTranslation(params['id'])).subscribe(slokaTranslation => this.slokaTranslation = slokaTranslation, 
                        error => this.errorMessage = <any>error
                      
        );
}
  
  

}

/*
@Pipe({

    name:'searchPipe',
    pure:false
})

export class SearchPipe implements PipeTransform{
    transform(canto:SlokaTest[], searchTerm:string):SlokaTest[]{
        
        return canto.filter(item=> {
         //   console.log(item);
            item.sloka.indexOf(searchTerm) !== -1;
        
        });
    }

}
*/


