import { Component,  Input, OnInit } from '@angular/core';
import * as $ from 'jquery';
import {SlokaService} from '../sloka-service.service';

@Component({
  selector: 'app-dict-view',
  templateUrl: './dict-view.component.html',
  styleUrls: ['./dict-view.component.css']
})
export class DictViewComponent implements OnInit {

  @Input() name: any;
  words: any;
  finalWords: any;
  modifiedName: any;
  dictConfig:any;
  errorMsg:any;
  window:any;

  constructor(public slokaService:SlokaService) {
    const thiz = this;
	this.window = window;
  }

  ngOnInit() {
    this.words = this.name.split('{');
    this.finalWords = this.words[0].split(/[-_]/);
    this.modifiedName = this.name.replace(this.words[0], '');
	localStorage.setItem('u-data', JSON.stringify(this.slokaService.dictUrlData));

	$(document).ready(function() {
      $('.sublist li a').click(function(event) {
		let data = event.target.innerHTML;
        data = data.replace(/[0-9]+$/gi, '');
		let urlData = JSON.parse(localStorage.getItem('u-data'));
		const url = urlData.url+data+'&'+urlData.parameters;
        window.open(url, 'DictView', 'width=560,height=340,toolbar=0,menubar=0,location=0');
        event.preventDefault();
      });
    });
  }

  showDic(val) {
  
    const url = 'http://sanskrit.uohyd.ac.in/cgi-bin/scl/e-readers/sbg/options1.cgi?word=' + val;
    window.open(url, 'DictView', 'width=560,height=340,toolbar=0,menubar=0,location=0');
  }

}



