import { Component, ViewChild ,OnInit ,Input } from "@angular/core";
import {NgbModal, ModalDismissReasons,NgbModalOptions} from '@ng-bootstrap/ng-bootstrap';

import { Table } from '../table';
import { DictViewComponent } from '../dict-view/dict-view.component';

@Component({

    selector: 'sloka-detail-view',
    templateUrl: './sloka-detail-child.component.html',
    styleUrls: ['./sloka-details-child.component.css']
})


export class SlokaDetailView implements OnInit {
    
    public fileName:string;
    public row1:boolean;
    public row2:boolean;
    public row3:boolean;
    public row4:boolean;
    public row5:boolean;
    public row6:boolean;
    public row7:boolean;
    public row8:boolean;
	public row9:boolean;
    public showList:string[];
    public options:NgbModalOptions;
    
    public showImagePath:string;
    
    @Input() slokaTranslation:Table[];
    @Input() title:string;
	@Input() vrittam:any;
	@Input() alankara:any;

    
constructor(private modalService: NgbModal){}
    
ngOnInit(){
    
    
   this.showList = ["row1","row6","row7","row8"];
    this.checkDefaultRows(); 
}
    
    
checkDefaultRows(){
    
    this.row1 = true;
    this.row6 = true;
    this.row7 = true;
    this.row8 = true;
    
}
    
show(name:string){
    
    if(this.showList != undefined ){
        return this.showList.indexOf(name) > -1 ? false : true;
    }else{
        return false;
    }
    
}
    
showRow(value:boolean,name:string){
    
    let pos = this.showList.indexOf(name);
    if(pos < 0){
        this.showList.push(name);
    }else{
        this.showList.splice(pos,1);
    }
    
}
    
mouseEnter(imagePath:string, content){
    
    this.options ={} 
    this.options.size ="lg";
    
    this.showImagePath = imagePath;
    this.modalService.open(content, this.options);
    
}
    
    
}




